import { Component, OnInit } from '@angular/core';
import { MisdatosService } from './misdatos.service';
import { ModalController } from '@ionic/angular';
import { ModalPasswordPage } from './modal/modalpassword.component';

@Component({
  selector: 'app-misdatos',
  templateUrl: './misdatos.page.html',
  styleUrls: ['./misdatos.page.scss'],
})
export class MisdatosPage implements OnInit {
  data: any;
  constructor(private service: MisdatosService, private modalController: ModalController) { }

  ngOnInit() {
    this.service.getDatos().subscribe((data: any) => {
      this.data = data;
    });
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: ModalPasswordPage,
      componentProps: { value: 123 }
    });
    return await modal.present();
  }


}
