import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MisdatosService {
  // apiUrl = 'http://127.0.0.1:8000/api/';
  private apiUrl = 'https://coagra.api.smartportal.cl/';
  constructor(private httpClient: HttpClient) { }

  public getDatos() {
    const ruta = 'clientes/datos_personales';
    const token = '?token=' + localStorage.getItem('token');
    return this.httpClient.get(this.apiUrl + ruta + token);
  }

  public getDatosLogin(rut){
    return this.httpClient.get(this.apiUrl + rut);
  }
}
