import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastService } from 'src/app/toast.service';
import { FacturaspagadasService } from 'src/app/facturaspagadas/facturaspagadas.service';

@Component({
    selector: 'modalpassword-page',
    templateUrl: './modalpassword.component.html',
    styleUrls: ['./modalpassword.scss']
})
export class ModalPasswordPage implements OnInit {
    submit = false;
    form: FormGroup;

    // tslint:disable-next-line: max-line-length
    constructor(private authService: AuthService, private fb: FormBuilder, private toast: ToastService, private loadingService: FacturaspagadasService) {
        this.form = this.fb.group({
            oldPassword: ['', [Validators.required, Validators.minLength(6)]],
            newPassword: ['', [Validators.required, Validators.minLength(6)]],
            renewPassword: ['', [Validators.required, Validators.minLength(6)]]
        });
    }

    ngOnInit() {
    }

    changePassword() {
        this.submit = true;
        this.loadingService.presentLoading('Cambiando contraseña...');
        const data = {
            pass_actual: this.form.value.oldPassword,
            pass1: this.form.value.newPassword,
            pass2: this.form.value.renewPassword,
            token: localStorage.getItem('token')
        };
        this.authService.changePassword(data).subscribe(res => {
            this.submit = false;
            this.loadingService.dismissLoading();
            this.toast.presentToast(res);
        }, error => {
            this.submit = false;
            this.loadingService.dismissLoading();
            console.log(error);
        });
    }
}
